import axios from 'axios';

let base = '';

export const toLogin = params => {
	return axios.post(`${base}/login`,params).then(res => res.data);
};

export const getUsers = params => {
	return axios.get(`${base}/user/list`,{params: params}).then(res => res.data);
};

export const delUsers = params => {
	return axios.get(`${base}/user/del`,{params: params}).then(res => res.data);
};

export const updateUsers = params => {
	return axios.get(`${base}/user/update`,{params: params}).then(res => res.data);
};

export const addUsers = params => {
	return axios.get(`${base}/user/add`,{params: params}).then(res => res.data);
};

export const upload = params => {
	return axios.post(`${base}/content/upload`,params).then(res => res.data);
};


