<template>
	<section class="listWrap">
		<el-col :span="24" class="toolbar">
			<el-form :inline="true" >
				<el-form-item>
				    <el-select class="select-search" v-model="typeName" @change="searchTypeChange"  placeholder="选择类型">
				      <el-option label="姓名" value="1"></el-option>
				      <el-option label="专业" value="2"></el-option>
				    </el-select>
				</el-form-item>
				<el-form-item>
				   <el-input  placeholder="关键字" v-model="filters.search"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="searchSumbit">查询</el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<!-- 教职工列表 -->
		<el-table :data="users" highlight-current-row style="width: 100%;" @selection-change="selectChange" v-loading="listLoading">
			<el-table-column type="selection" width="55">
			</el-table-column>
			<el-table-column prop="name" label="姓名" width="120">
			</el-table-column>
			<el-table-column prop="sexy" label="性别" :formatter="formatSex" width="80">
			</el-table-column>
			<el-table-column prop="age" label="年龄" width="70">
			</el-table-column>
			<el-table-column prop="classroom" label="专业" width="130">
			</el-table-column>
			<el-table-column prop="phone" label="手机号码" width="150">
			</el-table-column>
			<el-table-column prop="degree" label="学历">
			</el-table-column>
			<el-table-column label="操作" width="170">
				<template scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>
		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-button type="danger" :disabled="selects.length==0" @click="batchDel">批量删除</el-button>
			<el-pagination layout="prev, pager, next" :total="total" :page-size="20" @current-change="currentPageChange" style="float:right;">
			</el-pagination>
		</el-col>
		<!--编辑-->
		<el-dialog title="编辑" v-model="editFormVisible" :close-on-click-modal="false" :show-close="false">
			<el-form :model="editForm" label-width="80px" ref="editForm">
				<el-form-item label="姓名" prop="name">
					<el-input v-model="editForm.name"  auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="性别">
					<el-radio-group v-model="editForm.sexy">
						<el-radio class="radio" :label="1">男</el-radio>
						<el-radio class="radio" :label="0">女</el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="年龄">
					<el-input-number v-model="editForm.age" :min="0" :max="200"></el-input-number>
				</el-form-item>
				<el-form-item label="手机号码">
					<el-input type="text" v-model="editForm.phone" ></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="editFormVisible = false">取消</el-button>
				<el-button type="primary" :loading="editLoading" @click="editSubmit">提交</el-button>
			</div>
		</el-dialog>
	</section>
</template>
<script>
	import {getUsers, delUsers, updateUsers} from '../../api/api';
	export default {
		data() {
			return {
				listLoading: false,
				filters: {
					search: '',
					searchType: '0'
				},
				users: [],
				total: 0,
				pagenum: 1,
				typeName: '',
				selects: [],
				editForm: {
					id: 0,
					name: '',
					sexy: -1,
					age: 0,
					phone: ''
				},
				editFormVisible: false,
				editLoading: false
			}
		},
		methods: {
			formatSex: function (row, column) {
				return row.sexy == 1 ? '男' : row.sexy == 0 ? '女' : '未知';
			},
			getUsers: function(){
				let params = {};
				params.search = this.filters.search;
				params.searchType = this.filters.searchType;
				params.type = '1';
				params.pagenum = this.pagenum;
				this.listLoading = true;
				getUsers(params).then((res) => {
					this.total = res.total;
					this.users = res.users;
					this.listLoading = false;
				});
			},
			currentPageChange: function(currentPage){
				this.pagenum = currentPage;
				this.getUsers();
			},
			searchTypeChange: function(val){
				this.filters.searchType = val;
			},
			searchSumbit: function(){
				this.pagenum = 1;
				this.getUsers();
			},
			handleDel: function(index,row){
				this.$confirm('确认删除该记录吗?', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					let params = {type: '1', ids: (row.id).toString() };
					delUsers(params).then((res) => {
						this.listLoading = false;
						this.$message({
							message: '删除成功',
							type: 'success'
						});
						this.getUsers();
					});
				}).catch(() => {
				});
			},
			batchDel: function(){
				this.$confirm('确认删除选中记录吗?', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					let params = {type: '1', ids: this.selects.map(item => item.id).toString() };
					delUsers(params).then((res) => {
						this.listLoading = false;
						this.$message({
							message: '删除成功',
							type: 'success'
						});
						this.getUsers();
					});
				}).catch(() => {
				});
			},
			selectChange: function(sels){
				this.selects = sels;
			},
			handleEdit: function(index,row){
				this.editFormVisible = true;
				this.editForm = Object.assign({},row);
			},
			editSubmit: function(){
				if(!/^(13[0-9]|14[0-9]|15[0-9]|18[0-9])\d{8}$/.test(this.editForm.phone)){
					this.$message('请输入正确手机号码');
					return false;
				}
				this.$confirm('确认提交吗?', '提示').then(() => {
					this.editLoading = true;
					let data = Object.assign({}, this.editForm);
					let params = {type: '1', data: data };
					updateUsers(params).then((res) => {
						this.editLoading = false;
						this.$message({
							message: '删除成功',
							type: 'success'
						});
						this.editFormVisible = false;
						this.getUsers();
					});
				}).catch(() => {
				});
			}
		},
		mounted() {
			this.getUsers();
		}
	}
</script>
<style lang="scss" scoped>
	@import '../../style/list'
</style>


